@mixin gridBorder($position, $gap: var(--gap), $color: var(--light)) {
    position: relative;

    &:after {
        content: '';
        width: 1px;
        background-color: var(--light);
        height: 100%;
        position: absolute;
        top: 0;
        $position: calc( -1 * $gap / 2 );
    }
}