@import 'mixins.scss';

/* SETUP */
#front-page {
    max-width: 1140px;
    margin: 0 auto;
    padding: var(--gap);

    img {
        width: 100%;
        height: auto;
        display: block;
    }
    a {
        color: black;
        text-decoration: none;
    }
    section {
        margin-bottom: var(--gap);

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.article-category {
    font-family: var(--sans);
    text-transform: uppercase;
    font-size: 13px;
    font-weight: bold;
    color: var(--gray);
    line-height: 1em;
}

.article-title {
    transition: opacity 0.2s;
    padding: 0;
}

.article-meta {
    font-size: 15px;
    margin-bottom: var(--gap);
    display: block;
    font-family: var(--sans);

    > a {
        font-family: inherit;
        font-size: inherit;
        color: black;
        text-decoration: none;

        &:hover {
            color: var(--accent);
        }
    }
}

.article-excerpt {
    margin: 0;
    font-size: 16.5px;
}

.slideshow {
    position: relative;
    width: 100%;
    height: calc(100%);

    > img {
        position: absolute;
    }
}

/* DENSE */

section.dense {
    display: grid;
    grid-template-columns: 1fr 275px;
    grid-gap: var(--gap);
}

.dense.reverse {
    grid-template-columns: 275px 1fr;

    .dynamic-content {
        grid-row: 1;
    }
}

.dense-left {
    display: grid;
    grid-gap: var(--gap);
}

.dense-left-top {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: var(--gap);
}

.small-item {
    display: grid;
    grid-template-columns: 60px 1fr;
    grid-gap: var(--gap);
    align-items: center;
    color: black;
    text-decoration: none;
}

.small-item-title {
    font-family: var(--font);
    font-size: 16px;
    margin: 0;
    padding: 0;
    transition: all 0.2s;

    .article-category {
        margin-right: 5px;
    }
}

.small-item:hover > .small-item-title {
    opacity: var(--opacity);
}

.dense-left-bottom {
    display: grid;
    grid-template-columns: 4fr 3fr;
    grid-gap: var(--gap);
    color: black;
    text-decoration: none;
    align-items: center;

    &:hover .dense-title {
        opacity: var(--opacity);
    }
}

.dense-title {
    margin-top: var(--small-gap);
    margin-bottom: 10px;
    padding: 0;
    transition: all 0.2s;
    line-height: 1.25em;
}

@media all and (max-width: 1000px) {
    .dense-right {
        display: none;
    }

    section.dense {
        display: block;
    }
}

@media all and (max-width: 700px) {
    .dense-left-bottom img {
        margin-bottom: var(--small-gap);
    }

    .dense-left-top {
        display: none;
    }

    .dense-left-bottom {
        display: block;
    }

    .dense-left-bottom-title .article-category {
        display: block;
        margin-top: 22px;
    }
}

/* SECTION HEADER */

.section-header {
    margin: 0;
    padding: 0;
    display: flex;
    line-height: 1em;
    margin-bottom: calc(var(--gap) * 1.5);
    white-space: nowrap;

    &:after {
        content: "";
        width: 100%;
        height: 2px;
        margin-left: var(--small-gap);
        background-color: var(--accent);
        margin-top: auto;
        margin-bottom: 3px;
    }

    &.black-line:after {
        background-color: black;
    }
}

@media all and (max-width: 600px) {
    .section-header {
        white-space: normal;
    }
}

@media all and (max-width: 400px) {
    .section-header:after {
        content: none !important;
    }
}

/* GRIDS */

.grid {
    display: grid;
    grid-gap: var(--gap);

    &.borders {
        &.one-two {
            > *:first-child {
                @include gridBorder('right');
            }
        }
    }

    &.one-one { grid-template-columns: repeat(2, 1fr); }
    &.one-two { grid-template-columns: 1fr 2fr; }
    &.one-three { grid-template-columns: 1fr 3fr; }
    &.two-one { grid-template-columns: 2fr 1fr; }
    &.two-three { grid-template-columns: 2fr 3fr; }
    &.two-two-one { grid-template-columns: 2fr 2fr 1fr; }
    &.three-one { grid-template-columns: 3fr 1fr; }
    &.five { grid-template-columns: repeat(5, 1fr); }

    &.double-gap {
        grid-gap: var(--double-gap);
    }
}

/* END GRIDS */

/* STANDARD */
.standard {
    .article-title {
        font-family: var(--display);
        font-weight: normal;
        padding: 0;
        margin: 0;
        line-height: 1.2em;
    }
    .article-info {
        display: grid;
        grid-gap: 12.5px;
        color: black;
        text-decoration: none;
        height: fit-content;
    }

    &.reverse {
        grid-template-columns: 2fr 1fr;
        .article-info {
            grid-column: 2;
            grid-row: 1;
        }
        & > a {
            grid-column: 1;
            grid-row: 1;
        }
    }

    &.align-bottom { align-items: end; }
    &.align-middle { align-items: center; }

    &:hover .article-title {
        opacity: var(--opacity);
    }
}

@media all and (max-width: 1000px) {
    
}

/* CENTERED */
.centered {
    color: black;
    text-decoration: none;
    text-align: center;

    .article-title {
        font-family: var(--display);
        font-weight: normal;
    }

    .article-excerpt {
        font-size: 15px;
        max-width: 300px;
        margin: 0 auto;
    }

    .article-category {
        margin-bottom: -10px;
        display: block;
        margin-top: 20px;
    }
}

.stack {
    .stack-item {
        height: fit-content;
        &:not(:last-child) {
            margin-bottom: var(--gap);
        }
        .article-info {
            .article-title {
                font-family: var(--display);
                font-weight: normal;
                font-size: 18px;
                margin: 0;
                margin-bottom: 10px;
            }
            .article-excerpt {
                font-size: 15px;
            }
        }
        &:hover .article-title {
            opacity: var(--opacity);
        }
    }
}

/* COLUMNIST STACK */

.columnist-stack {
    .item {
        &:not(:last-child) {
            margin-bottom: var(--gap);
        }

        .avatar {
            border-radius: 50%;
        }

        .article-title {
            margin: 0;
            font-size: 21px;
            padding: 0;

            .columnist {
                color: var(--gray);
                font-family: var(--display);
                margin-right: 5px;
            }
        }


        &:hover {
            .article-title {
                opacity: var(--opacity);
            }
        }
    }
}